window.onload = () => {
    let iter = 1;

    const name = document.getElementById("employee-name");

    name.onclick = () => {
        window.$ajaxUtils.sendGetRequest("https://reqres.in/api/users/" + iter,
            (res) => {
            const json = JSON.parse(res.responseText)
            console.log(json);
        });

    }
}